import React, {Component} from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faEnvelope,} from '@fortawesome/free-solid-svg-icons'
import { faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons'
import { linkStyles, linkContainerStyles, headerStyles, iconStyles, avatarStyles, avatarContainerStyles, nameStyles } from './styles';
import Scene from '../Scene/Scene';

const links = [
  {to: "mailto:asjadanis128@gmail.com", label: 'asjadanis128@gmail.com', icon: faEnvelope, }, 
  {to: 'https://github.com/asjadanis', label: 'Github', icon: faGithub},
  {to: 'https://www.linkedin.com/in/asjad-anis/', label: 'LinkedIn', icon: faLinkedin},
]

class Header extends Component{
  constructor(props){
    super(props);
  }

  render(){
    return (
      <div className={headerStyles}>
        <div className={linkContainerStyles}>
          {links.map ((link,index) => {
            return(
              <a key={index} href={link.to} className={linkStyles} target={'_blank'} title={link.label  } >
                <span className={iconStyles}>
                  <FontAwesomeIcon icon={link.icon} />
                </span>
              </a>
            )
          })}
        </div>
        <div className={avatarContainerStyles} />
        <div className={avatarStyles('Assets/avatar.jpg')} />
        <span className={nameStyles(24, 25, 600)}> Asjad Anis </span>
        <span className={nameStyles(26, 20, 400)}> Software Engineer </span>
        <Scene />
      </div>
    )
  }
}

export default Header;