import {css, keyframes} from 'glamor';

let slide = keyframes({
  '0%':   { 'left': '0'},
  // '50%':  { 'left': '50%'},
  // '55%':  {'left': '45%'}, 
  '100%': { 'left': '44%'}
})

let spin = keyframes({
  '0%': {'transform': 'rotate(0deg)'},
  '100%': {'transform': 'rotate(360deg)'},
})

export const linkStyles = css({
  margin: '10px 10px 0px 0px', 
  color: 'white', 
  userSelect: 'none', 
  textDecoration: 'none',
  ':hover ': {
    color: 'dodgerblue',
    borderBottom: '1px solid dodgerblue'
  }
});

export const linkContainerStyles = css({
  position: 'absolute', 
  top: '5px',
  marginLeft: '1rem',
  // left: 'calc(100% - 110px)',
  display: 'flex', 
  animation: `${slide} 1.5s`,
  zIndex: 1
})

export const headerStyles = css({
  backgroundColor: 'black', 
  display: 'flex', 
  height: '250px',
  justifyContent: 'center',
  borderBottom: '3px solid #2197F3'
})

export const iconStyles = css({
  '& svg': {
    color: 'white', 
    width: '25px !important', 
    height: '25px !important',
    ':hover': {
      color: 'dodgerblue'
    }
  }
})

export const avatarStyles = (imageUrl) => css({
  width: '250px',
  height: '250px',
  borderRadius: '50%',
  position: 'absolute',
  // left: '42.3%',
  top: '7.32rem',
  backgroundImage: "url('"+imageUrl+"')",
  backgroundSize: 'cover',
  backgroundPositionX: 'center',
  backgroundPositionY: '40%',
  backgroundRepeat: 'no-repeat',
  zIndex: 1
});

export const avatarContainerStyles = css({
  width: '250px',
  height: '250px',
  borderTop: '5px solid dodgerblue',
  borderLeft: '5px solid #4caf50',
  borderBottom: '5px solid dodgerblue',
  borderRight: '5px solid #4caf50',
  borderRadius: '50%',
  position: 'absolute',
  // left: '42%',
  top: '7rem',
  animation: `${spin} 1.5s linear`,
  zIndex: 1
})

export const nameStyles = (top, size, weight) => css({
  position: 'absolute', 
  top: top+'rem', 
  fontSize: size+'px', 
  fontWeight: weight, 
  color: 'black',
  zIndex: 1
})