import React, {Component} from 'react';
import * as THREE from 'three';
import OrbitControls from 'three-orbitcontrols';

class Scene extends Component{
  constructor(props){
    super(props);
    this.state={
    }
    this.start = this.start.bind(this);
    this.stop = this.stop.bind(this);
    this.animate = this.animate.bind(this);
    this.renderScene = this.renderScene.bind(this);
    this.setupScene = this.setupScene.bind(this);
    this.destroyContext = this.destroyContext.bind(this);
    this.handleWindowResize = this.handleWindowResize.bind(this);
    this.onMouseMove = this.onMouseMove.bind(this);
  }

  componentWillMount(){
    window.addEventListener('resize', this.handleWindowResize)
  }

  componentDidMount(){
    this.setupScene();
  }

  setupScene(){
    this.width = this.container.clientWidth;
    this.height = this.container.clientHeight;

    const renderer = new THREE.WebGLRenderer({antialias: true });
    renderer.setPixelRatio(window.devicePixelRatio);
    renderer.shadowMap.enabled = true;
    renderer.gammaOutput = true;
    renderer.gammaFactor = 2.2
    renderer.shadowMap.type = THREE.PCFSoftShadowMap;

    let scene = new THREE.Scene();
    scene.background = new THREE.Color('black');

    let camera = new THREE.PerspectiveCamera(60, this.width/this.height, 0.25, 1000);
    scene.add(camera);

    let particleCount = 99999;
    let geometry = new THREE.Geometry();
    for (let i = 0; i < particleCount; i++){
      let vertex = new THREE.Vector3();
      vertex.x = Math.random() * 3000 - 1000;
      vertex.y = Math.random() * 3000 - 1000;
      vertex.z = Math.random() * 3000 - 1000;
      geometry.vertices.push(vertex);
    }

    let parameters = [
      {color: [1, 1, 0.5], size: 3},
      {color: [0.95, 1, 0.5], size: 2},
      {color: [0.90, 1, 0.5], size: 3},
      {color: [0.85, 1, 0.5], size: 2},
      {color: [0.80, 1, 0.5], size: 1}
    ]
    let parameterCount = parameters.length;
    let materials = [];
    let particles;
    for (let i = 0; i < parameterCount; i++) {

      let color = parameters[i].color;
      let size = parameters[i].size;

      materials[i] = new THREE.PointCloudMaterial({ color: color, size: size });

      particles = new THREE.PointCloud(geometry, materials[i]);

      particles.rotation.x = Math.random() * 3;
      particles.rotation.y = Math.random() * 8;
      particles.rotation.z = Math.random() * 4;

      scene.add(particles);
  }

    this.renderer = renderer;
    this.scene = scene;
    this.camera = camera;
    this.materials = materials;
    this.parameters = parameters;
    this.mouseX = 0;
    this.mouseY = 0;

    let spotLight = new THREE.SpotLight(0xffffff, 0.25)
    spotLight.position.set(45, 50, 15);
    camera.add(spotLight);
    this.spotLight = spotLight;

    let ambLight = new THREE.AmbientLight(0x333333);
    ambLight.position.set(5, 3, 5);
    this.camera.add(ambLight);

    this.renderer.setSize(this.width, this.height);
    this.container.appendChild(this.renderer.domElement);
    this.start();
  }

  start(){
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }

  renderScene(){
    this.renderer.render(this.scene, this.camera)
  }

  animate() {
    var time = Date.now() * 0.00005;
    this.frameId = requestAnimationFrame(this.animate);
    this.camera.position.x += (this.mouseX - this.camera.position.x) * 0.1;
    this.camera.position.y += (-this.mouseY - this.camera.position.y) * 0.1;

    this.camera.lookAt(this.scene.position);

    for (let i = 0; i < this.scene.children.length; i++) {
      var object = this.scene.children[i];
      object.rotation.y = time * (i < 4 ? i + 1 : -(i + 1));
    }

    for (let i = 0; i < this.materials.length; i++) {
      let color = this.parameters[i].color;
      let h = (360 * (color[0] + time) % 360) / 360;
      this.materials[i].color.setHSL(h, color[1], color[2]);
    }
    this.renderScene();
  }

  stop() {
    cancelAnimationFrame(this.frameId);
  }

  handleWindowResize(){
    let width = window.innerWidth;
    let height = window.innerHeight;
    this.camera.aspect = width/height;
    this.camera.updateProjectionMatrix();
  }

  onMouseMove(e){
    this.mouseX = ( e.clientX  );
    this.mouseY = ( e.clientY  );
  }

  componentWillUnmount(){
    this.stop();
    this.destroyContext();
  }

  destroyContext(){
    this.container.removeChild(this.renderer.domElement);
    this.renderer.forceContextLoss();
    this.renderer.context = null;
    this.renderer.domElement = null;
    this.renderer = null;
  }


  render(){
    const width = '100%';
    const height = '100%';
    return(
      <div
        onMouseMove={this.onMouseMove}
        ref={(container) => {this.container = container}}
        style={{width: width, height: height, position: 'absolute', overflow: 'hidden'}}
      >
      </div>
    )
  }

}

export default Scene;